.checkout_ad{
    object-fit: contain;
    height: 250px;
    margin-bottom: 10px;
}

.checkout{
    color:white;
}

.checkoutProduct_info{
    padding-left: 20px;
}

.checkoutProduct_info>button{
    background-color: #f0c14b;
    padding: 10px;
    border:1px solid #f0c14b;
    border-radius: 5px;
}

.checkoutProduct_title{
    font-size: 17px;
    font-weight: 800;
}
.basket_header{
    display: flex;
    flex: row;
   
}
.basket_total{
    flex:1;
    font-size: 24px;
    font-weight: 900;
    widows: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.basket_button{
    padding: 10px;
    color: black;
    background-color: #e7cf44;
    border: 1px solid;
    border-radius: 5px;
}