
.product{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    max-height: auto;
    min-width: 100px;
    background-color: rgba(104, 86, 86, 0.397);
    margin: 10px;
    z-index: 100;
    padding: 20px;
    border-radius: 10px ;
    color: white;
    
    
}

.product_price{
    margin-top: 10px;
}
.product_ratingImage{
    object-fit: contain;
    height: 10px;
}
.product_image{
    object-fit: contain;
    height: 300px;
    margin-bottom: 14px;
}
.product_info{
    height: 100px;
    margin-bottom: 15px;
}
.product > button{
    padding: 10px;
    color: black;
    background-color: #e7cf44;
    border: 1px solid;
   
}
.product:hover>button{
    cursor: pointer;
    background-color: #f7d723b9;
    border: 1px solid;
   
}

.product_gotocart{
    text-decoration: none;
    color:white;
}

