
.header{
    background-color: #131921;
    display: flex;
    align-items: center;
    position: sticky;
    top:0;
    z-index: 200;

}
.header_logo{
    width:100px;
    margin:0 20px;
    margin-top: 10px;
    cursor: pointer;
    object-fit: contain;
}

.header_searchIcon{
    background-color: #cd9042;
    padding: 5px;
    height:22px !important;
}
.header_search{
    display: flex;
    flex:1
}
.header_searchInput{
    height: 12px;
    padding: 10px;
    border:none;
    width: 100%;
}

.header_nav{
    display: flex;
    justify-content: space-evenly;
}

.header_link{
    text-decoration: none;
    color:white;
}

.header_option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
}

.header_optionLineOne{
    font-size: 10px;
}

.header_optionLineTwo{
    font-size: 13px;
    font-weight: 800;
}

.header_optionBasket{
    display: flex;
    text-decoration: none;
    align-items: center;
    color: white;
}

.header_basketCount{
    margin-right: 10px;
    margin-left: 10px;
    color:red
}

.header_searchInput:focus{
    outline: none;
    background-color: rgb(231, 224, 224);
}